.feature {
  display: grid;
  gap: 2.4375rem;
  padding: 3.125rem 2.375rem 2.375rem;
  box-shadow: 0 0.3125rem 0.9375rem #e6e6e6;
  border-radius: 1.25rem;
}
@media (min-width: 40em) {
  .feature {
    padding: 6.25rem 2.1875rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .feature__illustration-alt {
    order: 2;
  }
  .feature__content-alt {
    order: 1;
  }
}
@media (min-width: 71.875em) {
  .feature {
    padding: 3.125rem 2.1875rem 3.125rem 7.1875rem;
    gap: 8.75rem;
  }
}
.feature__illustration img {
  width: 100%;
  height: 100%;
}
.feature__content {
  place-self: center;
  text-align: center;
}
@media (min-width: 40em) {
  .feature__content {
    text-align: left;
  }
}
.feature__content h2 {
  margin-bottom: 1.3125rem;
  font-family: var(--font-headings);
  font-size: 1.3125rem;
}
@media (min-width: 71.875em) {
  .feature__content h2 {
    margin-bottom: 1.125rem;
    font-size: 1.8125rem;
  }
}
.feature__content p {
  color: var(--grayish-blue);
  line-height: 1.3;
  font-size: 0.9375rem;
}
@media (min-width: 71.875em) {
  .feature__content p {
    font-size: 1.125rem;
  }
}